<template>
    <v-content>
      <v-container fluid>
        <v-layout row justify-center>
          <v-form v-model="valid" xs8>
            <v-text-field
              label="Name"
              v-model="name"
              required
            ></v-text-field>
            <v-text-field
              label="Surname"
              v-model="lastname"
              required
            ></v-text-field>

            <v-btn @click="clear" flat color="orange">clear</v-btn>
            <v-btn
              @click="register"
              :loading="loading"
              @click.native="loader = 'loading'"
              :disabled="loading"
              color="orange"
            >
              submit
            </v-btn>
          </v-form>

          <v-snackbar
            :timeout="timeout"
            color="green"
            v-model="snackbar"
          >
            Player created, taking you back to players' list in a moment.
            <v-btn dark flat @click.native="snackbar = false">Close</v-btn>
          </v-snackbar>

        </v-layout>
        <div class="error" v-html="error"></div>
      </v-container>
    </v-content>
</template>

<script>
  import PlayersService from '@/services/PlayersService'

  export default {
    data () {
      return {
        name: '',
        lastname: '',
        error: null,
        loader: null,
        valid: false,
        loading: false,
        snackbar: false,
        timeout: 3000
      }
    },
    methods: {
      async register(){
        try {
          await PlayersService.register({
            name: this.name,
            lastname: this.lastname
          })
          this.snackbar = true
          setTimeout(() => {
            this.$router.push('/players')
          }, 3000);
        } catch(error) {
          this.error = error.response.data.error
        }
      },
      clear () {
        this.$refs.form.reset()
      }
    },
    watch: {
      loader () {
        const l = this.loader
        this[l] = !this[l]

        setTimeout(() => (this[l] = false), 3000)

        this.loader = null
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



